/**
* file: Page Loading animation style file
* date: 2020-07-21
* author: Frank
* lastModify: Frank 2020-07-21
*/

.loadingWrapper {
    height: 100vh;
    display: flex;
    align-items: center;
}

.loadingFormCol {
    width: 100%;
}

.loadingPageWrapper {
    margin: auto;
    width: 90%;
    text-align: center;
}

.loadingPageTitle {
    font: normal normal normal 50px poppins-semibold, poppins, sans-serif;
    letter-spacing: 1rem;
    margin-top: 0.5rem;
    color: #fd6b0b;
    font-size: 42px;
    @media screen and (max-width: 767px) {
        font-size: 19px;
    }
}

.loadingAnimation {
    font-family: Helvatica, lato, sans-serif;
    font-size: 14px;
    margin: auto;
    font-weight: bold;
    margin-top: 3rem;
}

.cubeGrid {
    width: 40px;
    height: 40px;
    margin: 10px auto;
}

@mixin loadingCube {
    width: 33%;
    height: 33%;
    background-color: #fd6b0b;
    float: left;
    -webkit-animation: loadingCubeGridScaleDelay 1.3s infinite ease-in-out;
    animation: loadingCubeGridScaleDelay 1.3s infinite ease-in-out;
}

.loadingCube1 {
    @include loadingCube;
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.loadingCube2 {
    @include loadingCube;
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.loadingCube3 {
    @include loadingCube;
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}

.loadingCube4 {
    @include loadingCube;
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.loadingCube5 {
    @include loadingCube;
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.loadingCube6 {
    @include loadingCube;
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.loadingCube7 {
    @include loadingCube;
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}

.loadingCube8 {
    @include loadingCube;
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.loadingCube9 {
    @include loadingCube;
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

@-webkit-keyframes loadingCubeGridScaleDelay {
    0%,
    70%,
    100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    }
    35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    }
}

@keyframes loadingCubeGridScaleDelay {
    0%,
    70%,
    100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    }
    35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    }
}

